.orderTrack {
  width: 500px;
  display: flex;
}

.orderStatus {
  width: 33.33%;
  height: 5px;
  background: #777;
  position: relative;
  font-size: 12px;
}

.orderStatus.active {
  background: #26a541;
}

.orderStatus:last-child {
  width: 0;
}

.orderStatus .point {
  width: 15px;
  height: 15px;
  position: absolute;
  border-radius: 8px;
  background-color: #777;
  top: -5px;
}

.orderStatus .point.active {
  background-color: #26a541;
}

.orderInfo {
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: -26px;
  transform: translateX(-50%);
}

.title {
  font-size: 14px;
  font-weight: 600;
}

.value {
  font-size: 12px;
  text-transform: capitalize;
}
